import { useState, useEffect } from "react";
import parse from 'html-react-parser';

const URL = 'https://api.elsevier.com/content/search/scopus?query=AU-ID(57201121715)%20OR%20AU-ID(23979068100)%20AND%20PUBYEAR%20%3E%202018&apiKey=3fae9ccf8b9324f5ec45e618050f5fe6&start=0&count=25';


export default function PublishPage() {
    const [entries,setEntries] = useState('')
    // const [numResult, setNumResult] = useState('')
    // const [url, setURL] = useState('')
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(URL)
            result.json().then(json => {
                console.log(json["search-results"].entry[0]["dc:creator"]);
                setEntries(json["search-results"].entry);
                // if (numResult === '') {
                //     console.log(1)
                // setNumResult(Number(json["search-results"]["opensearch:totalResults"]))
                // }
                console.log(json);
                // console.log(numResult);
            })
        }
        fetchData();
    }, []);
    // let URL = 'https://api.elsevier.com/content/search/scopus?query=AU-ID(57201121715)%20OR%20AU-ID(23979068100)%20AND%20PUBYEAR%20%3E%202018&apiKey=3fae9ccf8b9324f5ec45e618050f5fe6&start=25&count=25';
    // setURL(URL)
    // let num_i = []
    let authorList = []
    let titleList = []
    // let linkList = []
    // let yearList = []
    // let pubList = []
    // let volumeList = []
    
  

    for (let i=0; i < entries.length; i++) {
        authorList.push(entries[i]["dc:creator"]);
        titleList.push(parse(entries[i]["dc:title"].replace(/<inf[^>]*>/g,'<sub>').replace(/<\/inf>/g,'</sub>')));
    };

    return (
        <main>
            <h1>Publications</h1>
            <li className="alumni-info-1">Authors: {authorList[1]}</li>
            <li className="alumni-info-2">Title: {titleList[1]}</li>
        </main>
    )
}