import CCS_logo from './assets/CCS_logo.png';

export default function NewsTPL({title,summary,content,cover_img}) {
    return (
      <div>
        <h1>News</h1>
        <div className="post">
          <div className="image">
            <img className="cover-img" src={require('./assets/members_img/member_ccs.png')} />
          </div>
          <div className="texts">
            <h2>{title}</h2>
            <p className="summary">{summary}</p>
          </div>
        </div>
      </div>
        
    );
}