export default function ResearchPage() {
    return (
        <main>
            <body>
                <h1>Research</h1>
                <h2>Immiscible Fluid-Fluid Displacement in Porous Media for Energy Transition</h2>
                <p>To tackle this Energy Transition with UNs Sustainable Development Goals (SDGs), 
                    research group led by Dr. Tangparitkul focuses on elucidating immiscible displacement 
                    process in porous reservoir rock with fundamentals of interfacial and colloidal phenomena. 
                    We believe that while developed nations drive into more clean energies with climate concern, 
                    many developing countries remain to consume cheaper and more accessible fossil fuels. 
                    Current fluid-fluid displacements of interest are:</p>
                <h3>A. Oil-Water Displacement for SDG 7 (Affordable and Clean Energy)</h3>
                <p>The package aims to optimize petroleum production via optimizing tertiary recovery process 
                    called enhanced oil recovery (EOR) to be more easy-to-produce and less costly. 
                    Several chemicals have been used in our group, namely surfactants, low-salinity brines, 
                    and even nanoparticles, to reduce oil-water interfacial tension and induce wettability 
                    alteration  the main EOR mechanisms.</p>
                <h3>B. CO2-Brine Displacement for SDG 13 (Climate Action)</h3>
                <p>The group has been working on CO2 geological storage to permanently store the greenhouse gas 
                    underneath rock formation. Seal containment and storing reservoir confinement and storage 
                    capacity are the main focuses. Improvement on the injecting CO2 that displaces connate brine 
                    in rock is currently examined using many chemicals, i.e., surfactants and nanoparticles.</p>
            </body>
        </main>
        
    )
}